
body{
    display:flex;
    justify-content: center;
    margin-top:140px;
}
.errorhai{
    margin:20px;
    display: flex;
    justify-content: center;
}
.all{
    width:370px;
    height:450px;
    padding:40px;
    box-sizing: border-box;
    border:1px solid black;
    box-shadow: -10px 20px 30px #FB184E ,10px -20px 30px #b0b4ed;
    
}
.btns{
    width:300px;
    display:flex;
    gap:10px;
    justify-content: center;
    margin: auto;
    margin-bottom:30px;
}
.b{
    width:50px;
    height:40px;
    background-color: black;
    color:white;
}
.inputs{
    display: flex;
    flex-direction: column;
    width:70%;
    gap:40px;
    margin:40px auto;
    
}
.inputs input{
    height:30px;
    
}
h1{
    font-family: Montserrat;
font-size: 36px;
font-weight: 700;
line-height: 44px;
letter-spacing: 0em;
text-align: center;

}
.result{
    display: flex;
    justify-content: center;
    margin-bottom:10px;
}
